<template>
  <div id="app">
    <h1>Historia Medioambiental</h1>
    <div class="navbar">
      <router-link :to="{ name: 'ListPuntoInfo'}" class="nav-link">LISTADO DE PUNTOS</router-link>
      <router-link :to="{ name: 'HelloWorld'}" class="nav-link">Home</router-link>
      <router-link :to="{ name: 'SignUp'}" class="nav-link">Registrarse</router-link>
      <router-link :to="{ name: 'LogIn'}" class="nav-link">Iniciar Sesión</router-link>
    </div>
    <router-view/>
  </div>
</template>

<script>
import axios from 'axios';
  export default {
    name:'App',
    beforeCreate(){
      this.$store.commit('initializeStore')

      const token = this.$store.state.token

      if (token){
        axios.defaults.headers.common['Authorization'] = "Token "+ token
      } else {
        axios.defaults.headers.common['Authorization'] = ''
      }
    }
  }
</script>


<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.navbar {
  background-color:#42b983;
  padding: 30px;
}
.nav-link {
    font-weight: bold;
    color: #2c3e50;
}

#router-link-exact-active{
      color: #42b983;
    }
</style>



